.b1_card {
  width: 350px;
  height: 366px;
  background-color: #FFFFFF;
  box-shadow: #D8D8D8 0px 3px 10px;
  border-radius: 100px 0px 30px 0px;
  float: left;
  margin: 0px 30px 40px 0px;
  padding: 10px 40px;
}

.b1_number {
  font-size: 128px;
  color: #2EB738;
  font-family: Helvetica Neue;
  font-weight: bold;
  text-shadow:
    2px 2px #fff,
    -2px 2px #fff,
    2px -2px #fff,
    -2px -2px #fff,
    4px 4px 0px#F0F0F0;
  padding-left: 11px;
  margin: 0px;
}

.b1_result {
  font-size: 24px;
  color: #2EB738;
  font-weight: 300;
  font-family: Helvetica Neue;
  margin-bottom: 8px;
}
.b1_wrapper {
  width: 350px;
  height: 366px;
  float: left;
  margin: 0px 30px 40px 0px;
  position: relative;
}

.b1_x {
  color: #2EB738;
  font-size: 24px;
  position: absolute;
}

.b1_box {
  width: 282px;
  height: 338px;
  margin: 14px auto;
  border-top: 1px solid#2EB738;
  border-bottom: 1px solid#2EB738;
}

.b1_box-title {
  font-size: 56px;
  color: #2EB738;
  padding-top: 113.5px;
  margin: 0;
  font-weight: bold;
  font-family: Helvetica Neue;
}

.b1_box-sub {
  font-size: 24px;
  color: #2EB738;
  font-family: Helvetica Neue;
}
.b1_bg {
  background-color: #F0F0F0;
  margin: auto;
  padding: 80px 0px 0px 85px;
  width: 1280px;
  height: 1338px;
}

.b1_footer {
  margin: auto;
  width: 1280px;
  height: 34px;
  background-color: #2EB738;
}

.b1_footer-title {
  color: #F0F0F0;
  float: right;
  font-size: 16px;
  margin-right: 85px;
  padding-top: 8px;
}
.a {
  fill: #ff7600;
}

.b {
  fill: #fff;
}

.c {
  fill: #b1ff00;
  stroke: #fff;
  stroke-linecap: round;
}

.b2_wrapper {
  width: 100%;
  height: 100vh;
  background-color: #293b29;
  margin: auto;
  padding-top: 100px;
}

.b2_box {
  width: 600px;
  height: 600px;
  margin: auto;
  padding: 50px;
}
.b3_wrapper {
  width: 100%;
  height: 100vh;
  margin: auto;
  padding-top: 129px;
  background-color: #E8E8E8;
}

.b3_calculator {
  width: 350px;
  height: 525px;
  background-color: #062145;
  margin: auto;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 40%) 0px 20px 40px;
}

.b3_monitor {
  width: 100%;
  height: 109px;
  background-color: #041936;
  border-radius: 20px 20px 0px 0px;
}

.b3_procedure {
  color: #00C4FF;
  font-size: 16px;
  padding-top: 16px;
  padding-right: 16px;
  text-align: right;
}


.b3_result {
  color: white;
  font-size: 56px;
  padding-top: 20px;
  padding-right: 16px;
  text-align: right;
}

.b3_button-wrapper {
  width: 270px;
  float: left;
  cursor: pointer;
}

.b3_button {
  color: white;
  padding-top: 32px;
  padding-bottom: 32px;
  float: left;
  font-size: 24px;
  width: 90px;
  text-align: center;
  font-family: Roboto;
  font-weight: 100;
}

.b3_operate {
  color: white;
  padding: 24px 24px;
  font-size: 24px;
  width: 64px;
  text-align: center;
  float: left;
  margin: 8px;
  background-color: #041936;
  border-radius: 16px;
  cursor: pointer;
}

.b3_delete {
  color: #00C4FF;
  padding: 20px 24px;
  font-size: 24px;
  width: 90px;
  text-align: center;
  float: left;
  margin-top: 8px;
  border-radius: 16px;
  cursor: pointer;
}

.b3_equal {
  color: white;
  width: 162px;
  text-align: right;
  font-size: 24px;
  padding: 20px 24px;
  margin-right: 8px;
  margin-top: 8px;
  text-align: right;
  float: left;
  border-radius: 16px;
  background: -webkit-linear-gradient(left, #00C4FF, #6C00FF);
  background: linear-gradient(90deg, #00C4FF, #6C00FF);
  cursor: pointer;
}

.b3_button:hover,
.b3_operate:hover,
.b3_delete:hover,
.b3_equal:hover {
  opacity: 0.7;
}
